.gihubstats{
    padding: 0 3rem 0 3rem;
    
  margin-top: 10rem;
  margin-bottom: 10rem;
  padding-top: 1rem;
}
.gihubstats>h1{
    margin: 5rem 0rem 0rem 0rem;
    text-decoration: underline;
    color: var(--titletext);
}
.stats{
    width: 70%;
    margin: auto;
    /* border: 1px solid red; */
    margin-top: 2.5rem;
    padding: 1rem;
    border-radius: 2rem;
    box-shadow:var(--textboxbackgraund) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}
.stats>*{
    width: 100%;
margin: auto;

}
.graph{
    width: 70%;
    border-radius: 2rem;
    box-shadow:var(--textboxbackgraund) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
    margin-top: 2rem;
}
.graph>img{
    width: 100%;
    margin: auto;
    height: 40vh;
    
}
.gitdata{
    padding: 1rem;

    border-radius: 20px;
    box-shadow:var(--textboxbackgraund) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
    width: 70%;
text-align: center;
    margin: auto;
    margin-top: 2rem;
}
.gitdata>img{
    margin-top: 2rem;
    box-shadow: var(--textboxbackgraund) 0px 0px 5px 0px, rgba(0, 0, 0, 0.1) 0px 0px 1px 0px;
  
   
}


@media (min-width: 414px) and (max-width: 750px) {
    .gihubstats{
        padding: 0 3rem 0 3rem;
        
      margin-top: 0rem;
      margin-bottom: 2rem;
      padding-top: 1rem;
    }
    .stats{
        width: 90%;
        margin: auto;
        /* border: 1px solid red; */
        font-size: 1rem;
        margin-top: 2rem;
        padding: 2rem;
        border-radius: 20px;
        box-shadow:var(--textboxbackgraund) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
    }

    .stats>*{
        width: 100%;
    margin: auto;
    font-size: 5rem;
    }

    .graph{
        width: 90%;
        margin: auto;
        padding: 1rem;
        margin-top: 2rem;
    }
    .graph>img{
        height: 25vh;
    }
    .gitdata{
        border-radius: 2rem;
        width: 90%;
    }
    /* .gitcalender{
        font-size: 78rem;
    } */
    
}



