.App {
  text-align: center;
  margin: 0px;
  /* width: 100vw; */
  padding: 0px;
/* border: 1px solid red; */
background-color: var(--homegradient);
}
::-webkit-scrollbar{
  width: 12px;
  /* border: 1px solid rgb(255, 255, 255); */
}
::-webkit-scrollbar-thumb{
  border-radius: 10px;
  border: none;
  background:linear-gradient(transparent, #515ada 80%);
}
::-webkit-scrollbar-thumb:hover{
  background:linear-gradient(transparent, #bf32df 80%);
}
.title_div{
  color: var(--titletext);
  text-align: center;
  /* padding: 20px ; */
  padding: 1rem 0rem 0rem 0rem;
}
.title_div>:nth-child(1){
  font-size:3rem;
}
.title_div>:nth-child(2){
  font-size:1.5rem;
  margin-bottom: 2rem;
}
.light_theam{
  --homegradient:#ececf5;
  --aboutgradent:#d2d2e4;
  --titletext:#1a1f71;
--primarybackgraund:#6F00FF;
--textboxbackgraund:#7B68EE;

--overtext:rgb(255, 255, 255);
--gradient:linear-gradient(90deg, #efd5ff 5%, #515ada 100%);
}

.dark_theam{
  --backgraundshadow: #443d0a;
  --primarybackgraund: #4D4DFF;
  --backgraundcolor:#dadada;
  --homegradient:#4f4f52;
}


hr{
  margin: 4rem;
}