#About{
    /* background-color: var(--aboutgradent);/ */
    height: 100vh;
    /* margin-top: 200px; */
    /* border: 1px solid red; */
}


.compotitile{
    text-align: start;
    color: red;
}
.contentdiv{
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    align-items: center;
}
.contentdiv>img{
    width: 40%;
    height: 70vh;
  
border-radius: 50%;
}
.contentdiv>div{
    width: 50%;
    /* background-colo/r: var(--textboxbackgraund); */
    /* border: 1px solid red; */
    text-align: start;
    padding: 2.2rem;
    color: var(--overtext);
    background-color: #21D4FD;
    border-radius: 10%;
background-image: linear-gradient(19deg, #21D4FD 0%, #B721FF 100%);

}

.aboutmassage{
    line-height: 2.2rem;
    word-spacing: 0.7rem;
    font-family: sans-serif;
    color: var(--infotext);
}


@media (min-width: 414px) and (max-width: 767px) {
    #About{
        height: 95vh;
        /* border: 1px solid black; */
        /* margin-top: 4rem; */
    }
   
    .contentdiv{
        display: flex;
        width: 90%;
        height: 70vh;
        /* border: 1px solid black; */
        margin: auto;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .contentdiv>img{
      display: none;
    }
    .contentdiv>div{
        width: 95%;
        text-align: center;
        padding: 2.2rem;
        height: 70vh;
    }
    .aboutmassage{
        line-height: 1.5rem;
        word-spacing: 0.7rem;
        font-size: 1.5rem;
        
    }
}