*,
*::after,
*::before {
  margin: 0;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
}
body {
  font-size: 1.6rem;
}
li {
  list-style: none;
  color: var(--overtext);
}
.resumeid>a{
  text-decoration: none;
}
.resumeid>a>*{
  color:var(--homegradient);
  font-size:1rem;
}
.resumeid:hover{
background: var(--gradient);
}
.logo{
  width: 70px;
}
.nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 8vh;
  width: 100%;
  top: 0;
  position: fixed;
  z-index: 551;
  background-color: #21D4FD;
background-image: linear-gradient(19deg, #21D4FD 0%, #B721FF 100%);

  /* background:/var(--primarybackgraund); */
}
.nav__brand {
  /* border: 1px solid red; */
  text-transform: uppercase;
display: flex;
justify-content: space-evenly;
width: 20%;
}
.nav__item{
    cursor: pointer;
}
.nav__menu {
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: 3rem;
 
}
.nav__toggler {
  display: none;
 
}
.nav__toggler div {
  width: 2.5rem;
  height: 0.2rem;
  margin: 0.4rem;
  background: rgb(177, 177, 177);
  transition: 0.3s ease-in;
}

@media screen and (max-width: 768px) {
  .nav__toggler {
    display: block;
    cursor: pointer;
    
  }
  .nav__menu {
    position: fixed;
    top: 8vh;
    right: 0;
    height: 92vh;
    width: 35vw;
    /* border: 1px solid rgb(0, 0, 0); */
    flex-direction: column;
    background-color: #ca72f3;
    transform: translateX(100%);
    transition: 0.5s ease-in;
  }
}
/* Active Class */
.nav__active {
  transform: translateX(0%);
}

/* Toggle Icon Animation */

.toggle .line1 {
  transform: rotate(-45deg) translate(-4px, 5px);
  border: 1px solid rgb(0, 0, 0);
}
.toggle .line2 {
  opacity: 0;
}
.toggle .line3 {
  transform: rotate(45deg) translate(-4px, -5px);
}