.Project {
    align-items: center;
    width: 100%;
    /* padding: 0.5rem 3.5rem; */
    margin-top: 5rem;
    margin-bottom: 5rem;
    padding-top: 3rem;
  }
  .Projectdiv{
    width:75%;
    margin: auto;
    margin-top: 10px;
    margin-bottom:4rem;
    height:55vh;
    padding: 0.8rem;
    border-radius: 1.2rem;
  align-items: center;
  justify-content: center;
    /* background-color:var(--black);  */
  display: flex;
  box-shadow:var(--textboxbackgraund) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}
  
  
  .imgdiv{
    width: 50%;
    margin: auto;
    height: 85%;
   
    border-radius: 1.2rem;
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
    /* border: 1px solid rgb(225, 193, 193); */
    background-color: beige;
  
  }
  .projectimg{
    width: 100%;
    height: 100%;
    margin: auto;
    border-radius: 1.2rem;
  
    /* border: 1px solid rgb(132, 0, 255); */
  }
  .projectimg:hover{
    transform: scale(1.01);
    transition: 1s;
  }
  .descriptiondiv{
    display: flex;
    width: 45%;
    align-items: center;
    justify-content: center;
    margin: auto;
    height: 100%;
    color: black;

    /* padding: 10px 0px 10px 0px; */
    /* border: 1px solid rgb(225, 193, 193); */
    /* box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 2px, rgba(0, 0, 0, 0.07) 0px 2px 4px, rgba(0, 0, 0, 0.07) 0px 4px 8px, rgba(0, 0, 0, 0.07) 0px 8px 16px, rgba(0, 0, 0, 0.07) 0px 16px 32px, rgba(0, 0, 0, 0.07) 0px 32px 64px; */
  }

  .textdata{
    text-align: start;
    width: 100%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    justify-content: space-between;
    /* border: 1px solid rgb(68, 255, 0); */
  }
  .textdata>:nth-child(1){
  text-align: center;
  
  }
  .project_title{
    color: #52c5ff;
    font-size: 3rem;
  }
  
  .project_title>span{
    font-size: 1.2rem;
  }
  .textdata>:nth-child(2){
    font-size: 2.5rem;
  
  }
  .textdata>:nth-child(3){
    font-size: 1.5rem;
  }
  .highlight{
    color: brown;
    font-size: 1.6rem;
    } 
  
  .btns{
    width: 40%;
    display: flex;
    text-align: start;
    justify-content: space-between;
    margin: auto;
    /* margin-top: 20px; */
    height: 4rem;
    border: 1px solid rgb(255, 200, 0);
  }
  .icon{
    height: 4rem;
    width: 2rem;
   
  }
  .projectdecdata{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    padding: 1rem;
  }
  .projectdecdata>*{
    font-size: 1.5rem;
  }
  .button{
    width: 100%;
  }
  .imgtext_stackdiv{
    width: 60%;
    border: 1px solid red;
    display: flex;
    justify-content: space-between;
  }
  .imgtext_stackdiv>div{
    border: 1px solid red;
    background-color: black;
  
  }
  .imgtext_stackdiv>div>img{
    width: 4rem;
  }
  
  @media (min-width: 414px) and (max-width: 767px) {
    .Project {
      align-items: center;
      margin-bottom: 0rem;
      margin-top: 0rem;
      /* margin-bottom: 12rem; */
    }
    .Projectdiv{
      width:75%;
      height:75vh;
      margin-top: 1rem;
      flex-direction: column;
      /* padding: 2rem 0rem ; */
      padding-top: 2rem;
  }
  .imgdiv{
    width: 90%;
    margin: auto;
    height: 45vh;
    border-radius: 1.2rem;
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
    /* border: 1px solid rgb(225, 193, 193); */
    background-color: beige;
  }
  .projectimg{
    width: 100%;
    height: 100%;
    margin: auto;
    border-radius: 1.2rem;
    /* border: 1px solid rgb(132, 0, 255); */
  }
    .gridDiv{
      grid-template-columns: repeat(1,1fr);
      grid-template-rows: repeat(3,80vh);
      gap: 1rem;
      width: 100%;
    }
  .projetbox{
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;  
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 10px 0px 10px 0px;
    border-radius: 30px;
  }
  .descriptiondiv{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    margin: auto;
    color: black;
  }
    .textdata{
      height: 40%;
      width: 90%;
      margin: auto;
    font-size: 10px;
    gap: 0.5rem;
    margin-top: 10px;
    text-overflow: inherit;
    }
    .highlight{
      color: brown;
      font-size: 1rem;
      }
      .btns{
        display: flex;
        justify-content: space-between;
        width: 60%;
        margin: auto;
        margin-top: -20px;
      }
      .btns>button{
        border-radius: 10px;
        font-size: 14px;
      }
      .project_title{
        font-size: 2rem;
      }
      .info1{
        font-size: 1.2rem;
      }
      .techstack{
        font-size: 1.2rem;
      }
      .btns{
        width: 40%;
        display: flex;
        justify-content: space-between;
        margin: auto;
        /* border: 1px solid rgb(255, 200, 0); */
      }
      .button{
        width: 100%;
        font-size: 1.2rem;
      }
  
  }