.contact-form {
  width: 100%;
}

.contact_data {
    justify-content: center;
    display: flex;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    width: 70%;
    padding: 1rem 4rem;
    /* height: 90vh; */
    margin: auto;
    /* margin-bottom: 100px; */
  }
.followme{
    /* border: 1px solid black; */
    width: 35%;
    margin: auto;
    /* height: 40vh; */
}

.icondiv{
    display: flex;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    align-items: center;
    padding: 10px;
    margin-top: 10px;
    width: 100%;
 
}
.icondiv>:nth-child(1){
    width: 40px;
    color: var(--primarybackgraund);
    /* background:var(--primarybackgraund); */
    height: 50px;
}
.icondiv>:nth-child(2){
  text-align: start;
    margin-left: 2rem;
    font-size: 16px;
    color: black;
    text-decoration: none;
    
    /* border: 1px solid red; */
}
.formdiv{
    /* border: 1px solid rgb(41, 255, 17); */
    width: 50%;
    display: flex;
    height: 60vh;
    margin: auto;
justify-content: center;

}

.formdiv>form{
margin-top: 50px;
    /* border: 1px solid red; */
    text-align: center;

}
.user {
  width: 90%;
  height: 4rem;
  margin: 10px;
  padding: 0.3em;
  font-size: 16px;
  background: transparent;
  border-radius: 8px;
  border: 1px solid rgb(190, 71, 219);

}
.user:focus{
  border: 1px solid black;
}
.button1 {
  width: 40%;
  margin: auto;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 5%;
  background: transparent;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.button1:hover{
 background-color:  var(--primarybackgraund);
 color: white;
}
textarea {
  height: 10rem !important;
}

@media (min-width: 414px) and (max-width: 750px) 
  {
    .followme{
     display: none;
  }
  .contact_data {
    width: 75%;
    padding: 1rem 2rem;
    border-radius: 10%;
  }
  .formdiv{
    /* border: 1px solid rgb(41, 255, 17); */
    width: 100%;
    display: flex;
    height: 70vh;
    margin: auto;
justify-content: center;

}

.formdiv>form{
margin-top: 50px;
    /* border: 1px solid red; */
    text-align: center;

}

  


}
