#Home{
    /* border: 1px solid black; */
    height: 100vh;
    position: relative;
   top: 3rem;
   /* border: 1px solid red; */
   background-color:var(--homegradient);
   margin-bottom:10rem;
}
.h_contentdiv{
    display: flex;
    width: 90%;
    margin: auto;
    height: 90vh;
justify-content: space-between;
}
.h_left{
    /* border: 1px solid red; */
    width: 50%;
    text-align: start;
    display: flex;
    flex-direction: column;
align-items: center;
justify-content: center;
}
.h_right{
    /* border: 1px solid green; */
    width: 60%;
    height: auto;
    padding: 1rem;
display: flex;
align-items: center;
    background-color: linear-gradient( rgba(255,0,0,0), rgba(255,0,0,1));
}
.myname{
    text-align: center;
    font-size: 7rem;
color: #0396FF;

}
.typing{
    text-align: center;
    margin-top: 1rem;
    font-size:2rem;
}
.startname{
    text-align: start;
    margin-left: -12rem;
}

.startname>:nth-child(1){
    font-size: 3.2rem;
    color: #6F00FF;
}
.startname>:nth-child(2){
    font-size: 2.5rem;
}




.heroimg{
    width: 50%;
    /* margin-top: 30px; */
    border-radius: 10%;
    margin: auto;
    /* border: 1px solid red; */
}

@media (min-width: 414px) and (max-width: 767px) {
 
    #Home{
        border: 1px solid rgb(255, 255, 255);
        top: 0rem;
        margin: auto;
        margin-top:4rem;
        margin-bottom:2rem;
        height: 90vh;
    }

    .h_contentdiv{
        display: flex;
        flex-direction: column;
        width: 95%;
        margin: auto;
        margin-top: 4rem;
        align-items: center;
        height: 75vh;
      
    justify-content: space-between;
    }
    .h_left{
        /* border: 1px solid red; */
        width: 90%;
        margin: auto;
        text-align: start;
        /* align-items: s; */
    }
    .h_right{
        /* border: 1px solid green; */
        width: 90%;
        /* height: auto; */
        margin: auto;
        padding: 1rem;
        margin-top: 2rem;
    display: flex;
    align-items: center;
        background-color: linear-gradient( rgba(255,0,0,0), rgba(255,0,0,1));
    }

    
    .myname{
        text-align: center;
        font-size: 4.5rem;
    }
    .startname{
        /* margin-left: -2rem; */
        text-align: start;
    }
  
.startname>:nth-child(1){
    font-size: 1.2rem;
}
.startname>:nth-child(2){
    font-size: 1.5rem;
}

.heroimg{
    width: 25rem;
    /* margin-top: 30px; */
    border-radius: 20%;
    margin: auto;
    /* border: 1px solid red; */
}
.typing{
    text-align: center;
    margin-top: 1rem;
    font-size:1.2rem;
}

}